/* Imports*/
@import url("https://fonts.googleapis.com/css2?family=Nosifer&display=swap");
/* Variables */
/* Body */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #fff;
}

/* Header */
header {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 10px;
  padding: 10px;
  background-color: lightblue;
  display: grid;
  grid-gap: 0;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "logo title";
}
header img {
  grid-area: logo;
}
header h1 {
  font-family: "Nosifer", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 100;
  color: #4c96d7;
  grid-area: title;
}

/* Footer */
footer {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  margin: 10px;
  padding: 10px;
  background-color: lightblue;
}
footer a {
  color: black;
  text-decoration-line: none;
}
footer a:hover {
  text-decoration-line: underline;
}

